import React, {useState} from 'react';
import style from './offer.module.scss'
import {Diagram} from "./components/Diagram/Diagram";
import {Button} from "../../Components/button.jsx/Button";
import {ReactComponent as Lion} from "../../assets/pictures/svg/lion/  offer.svg";
import {ReactComponent as LionBig} from "../../assets/pictures/svg/lion/  offer big.svg";
import {Modal} from "../../Modal/Modal";
import {Form} from "./components/form/Form";
import {SuccessForm} from "./components/successForm/SuccessForm";
import {Element} from "react-scroll";


export const Offer = () => {
    const [isModal, setIsModal] = useState(false);
    const [isSuccess, setIsSuccess] = useState(false)

    const onModal = () => {
        setIsModal(prevState => !prevState)
    }

    const setSuccess = () => {
        setIsSuccess(prevState => !prevState)
    }

    const submitForm = () => {
        setIsModal(prevState => !prevState);
        setIsSuccess(prevState => !prevState)
    }
    return (
        <>
            <Element name='course'></Element>
            {isModal && <Modal>{isSuccess ? <SuccessForm setSuccess={submitForm}/> :
                <Form formSubmit={setSuccess} setModal={onModal}/>}</Modal>}
            <section className={style.offerWrapper}>
                <div className={style.offer}>
                    <Lion className={style.lion}/>
                    <LionBig className={style.bigLion}/>
                    <div>
                        <h3> </h3>
                        <h4>  Front-end   </h4>
                        <p className={style.text}>  .       ITEC , 
                                     .</p>
                        <p className={style.text}>        Python, 
                            -   Django,    ,  -,
                             -API,  Celery, HTTP, Docker, GitHub  Postman.</p>
                    </div>

                    <Diagram/>

                </div>
                <div className={style.button}>
                    <Button callback={onModal} text=" "/>
                </div>
            </section>
        </>
    );
};

